.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.form-container {
  width: 300px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f1f1f1;
}

.form-heading {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  margin-bottom: 10px;
}

label {
  display: block;
  margin-bottom: 1px;
  align-self: center;
  font-size: 27px;
  font-weight: 600;
  margin-right: 22px;
}

input {
  width: 94%;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.form-button {
  width: 100%;
  padding: 10px;
  background-color: #4caf50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.form-button:hover {
  background-color: #45a049;
}

.admin-button {
  background-color: #ccc;
  color: #000;
}
.admin-button:hover {
  background-color: #282c34;
  color: #FFF;
}
.logout-button:hover {
  background-color: red;
}

.form-switch {
  text-align: center;
}

.form-switch button {
  background: none;
  border: none;
  color: blue;
  cursor: pointer;
}

.form-switch button:hover {
  text-decoration: underline;
}
